#container {
  display: inline-block;
  position: relative;
}

#container figcaption {
  position: absolute;
  bottom: 7%; /* Alinha o texto 10% a partir da parte inferior do contêiner pai */
  left: 50%; /* Alinha o texto no centro horizontal do contêiner pai */
  transform: translateX(-50%); /* Move o texto de volta pela metade da largura do próprio texto */
  font-size: 3vw; /* Usa uma unidade de largura de viewport para o tamanho da fonte */
  color: black;
  text-shadow: 0px 0px 5px black;
  white-space: nowrap; /* Impede a quebra de texto em várias linhas */
} 
